import React from 'react';
import { connect } from "react-redux";


 
class Counter extends React.Component {
        constructor(){
          super();
        }

 render() {
    const { operators, oper, oneToNine, count, num1to9, clear, equal, period, zero, finalAnswer } = this.props;
    let dummy;
    if(finalAnswer !='.' && !(/[.]+/g.test(finalAnswer))){
      dummy= <button id='decimal' onClick={period.bind(this)}> <strong>.</strong> </button>   ; 
     } else{
       dummy= <button id='decimal' > <strong>.</strong> </button>   
     }
    return (
      <div id='filler'>
    <div id="calculator">
      <div id='display'>{count}</div>
      <div className='output'>{finalAnswer}</div>       
        <button id='clear' onClick={clear.bind(this)}> <strong>Clear</strong> </button>
        <button id='equals' onClick={equal.bind(this)}> <strong>=</strong> </button>  
        <div id="op-btns">
            {operators.map((d)=>{
                   return(
       <button id={d.id} onClick={ oper.bind(this, d.op)}><strong>{d.show}</strong></button>            
                   )
        })}
        </div>
    <div id= 'hmm'>       
      <div id="numba-btns">
            {oneToNine.map((item) => {
                  return(
       <button id={item.id} onClick={ num1to9.bind(this, item.num) }><strong>{item.num}</strong></button>
                 )})}
         </div>
            </div>
      <div id="other-btns">
       <button id='zero' onClick={zero.bind(this)}> <strong>0</strong> </button> 
      {dummy}
        </div>
      </div> 
        <div className ="author"> Created by<br /> 
        <a target="_blank" href="https://pardesiadventures.com/">
          Farooq Tariq
         </a> 
        </div>      
    </div>
  )
 }
}

const mapStateToProps = state => ({
  count: state.count,
  finalAnswer: state.finalAnswer,
  oneToNine: state.oneToNine,
  operators: state.operators

});

const mapDispatchToProps = (dispatch, ) => {
  return{
    num1to9: (text) => {
        dispatch({
        type: 'NUM1TO9',
        payload:text
      });
    },
    clear: () => {
      dispatch({
        type: 'CLEAR'
      })
    },
    oper: (text) => {
      dispatch({
        type: 'OPER',
        payload:text
      })
    },
    equal: () => {
      dispatch({
        type: 'EQUAL',
         })
    },
    period: () => {
           dispatch({
        type: 'PERIOD',
       });   
    },
     zero: () => {
     dispatch({
        type: 'ZERO',
         })
      },    
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Counter);





