import React from 'react';
import { render } from 'react-dom';
import Counter from './Counter';
import { createStore, combineReducers } from "redux";
import { Provider } from "react-redux";
import './index.css';

const things = {
    count: '',
    finalAnswer: '0',
    oneToNine: [ {num:"7", id:"seven"},{num:"8", id:"eight"},{num:"9", id:"nine"},{num:"4", id:"four"},{num:"5", id:"five"},{num:"6", id:"six"},{num:"1", id:"one"},{num:"2", id:"two"},{num:"3", id:"three"} ],
    operators:[{op:"/", id:"divide",show:'/'},{op:"*", id:"multiply",show:'X'},{op:"-", id:"subtract", show:'-'},{op:"+", id:"add", show:'+'}, ]
  }

const reducer = (state = things, action) => {
    switch(action.type){  
    case "CLEAR":
        return {
            ...state,
            count: '0',
            finalAnswer:'0'
        };
    case "NUM1TO9":
        return {
            ...state,
            count: (state.count + action.payload).replace(/^0/,''),
            finalAnswer: ((state.finalAnswer + action.payload).replace(/^0/,'')).replace(/^[+-/*]/,'')
        }; 
    case "OPER":
        return {
           ...state,
            count: (state.count + action.payload).replace(/[-+/*]+$/,action.payload),
            finalAnswer: action.payload
        };
    case "EQUAL":
        return {
              ...state,
              count:  eval(state.count), 
              finalAnswer: eval(state.count)
        }; 
    case "PERIOD":
         return {
           ...state,   
          count:  state.count + '.',
          finalAnswer:  (state.count + '.')
       }   
    case "ZERO":
        return {
             ...state,
           count: (state.count +'0').replace(/^0/, ''),
           finalAnswer:(state.count +'0').replace(/^0/, '')
        };            
    default:
        return state;    
     }
  }



const store = createStore(reducer);



const App = () => (
  <Provider store={store}>
  <div>
    <Counter />
  </div>
  </Provider>  
);

render(<App />, document.getElementById('root'));


